import React, { useState } from 'react';
import { backendUrl } from '../config/api';
import './UserProfile.css'; // Asumiremos que crearemos este CSS o usaremos estilos en lnea

const UserProfile = ({ user, onClose, onLogout, onUpdateUser }) => {
    const [isEditing, setIsEditing] = useState(false);
    const [newName, setNewName] = useState(user.name || '');
    const [isLoading, setIsLoading] = useState(false);

    const handleSaveName = async () => {
        setIsLoading(true);
        try {
            const response = await fetch(backendUrl('profile.php'), {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ name: newName })
            });
            const data = await response.json();
            if (data.success) {
                onUpdateUser({ ...user, name: newName });
                setIsEditing(false);
            } else {
                alert('Error: ' + data.message);
            }
        } catch (error) {
            alert('Error al actualizar perfil');
        } finally {
            setIsLoading(false);
        }
    };

    return (
        <div className="user-profile-overlay" onClick={onClose}>
            <div className="user-profile-modal" onClick={e => e.stopPropagation()}>
                <div className="profile-header">
                    <img src={user.picture} alt="Perfil" className="profile-pic-large" />
                    {isEditing ? (
                        <div className="edit-name-container">
                            <input
                                type="text"
                                value={newName}
                                onChange={e => setNewName(e.target.value)}
                                className="edit-name-input"
                            />
                            <button onClick={handleSaveName} disabled={isLoading}>Guardar</button>
                            <button onClick={() => setIsEditing(false)}>Cancelar</button>
                        </div>
                    ) : (
                        <h3 onClick={() => setIsEditing(true)} title="Clic para editar">
                            {user.name} 
                        </h3>
                    )}
                    <p>{user.email}</p>
                </div>

                <div className="profile-actions">
                    <button className="btn-logout" onClick={onLogout}>Cerrar Sesin</button>
                    <button className="btn-close" onClick={onClose}>Cerrar</button>
                </div>
            </div>
        </div>
    );
};

export default UserProfile;
