// src/pages/ProductListPage.js
import React, { useState, useEffect } from 'react';
import { useSearchParams, useLocation } from 'react-router-dom';
import ProductCard from '../components/ProductCard';
import { backendUrl } from '../config/api';
import './ProductListPage.css';

function ProductListPage() {
  const [products, setProducts] = useState([]);
  const [categories, setCategories] = useState([]);
  const [selectedCategoryId, setSelectedCategoryId] = useState('');
  const [sort, setSort] = useState('newest'); // newest | price_asc | price_desc | name
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);
  const [searchParams] = useSearchParams();
  const location = useLocation();

  // Estados para filtros avanzados
  const [showFilters, setShowFilters] = useState(false);
  const [brands, setBrands] = useState([]);
  const [filterMinPrice, setFilterMinPrice] = useState('');
  const [filterMaxPrice, setFilterMaxPrice] = useState('');
  const [filterBrandIds, setFilterBrandIds] = useState([]); // Array de IDs seleccionados

  // Soportar parmetros iniciales opcionales
  const categoryParam = searchParams.get('category_id') || searchParams.get('category') || '';
  const sortParam = searchParams.get('sort') || '';
  const searchParam = searchParams.get('search') || ''; // Leer parmetro de bsqueda

  useEffect(() => {
    const fetchProducts = async () => {
      // 1. Inicia la carga y limpia errores/productos anteriores
      setIsLoading(true);
      setError(null);
      let apiUrl = backendUrl('get_products.php');
      const params = new URLSearchParams();
      if (selectedCategoryId) params.append('category_id', selectedCategoryId);
      if (sort) params.append('sort', sort);
      if (searchParam) params.append('search', searchParam); // Aadir bsqueda a la API

      // Aadir filtros avanzados
      if (filterMinPrice) params.append('min_price', filterMinPrice);
      if (filterMaxPrice) params.append('max_price', filterMaxPrice);
      if (filterBrandIds.length > 0) params.append('brand_id', filterBrandIds.join(','));

      const qs = params.toString();
      if (qs) apiUrl += `?${qs}`;

      try {
        // Log para depuracin: URL y parmetros usados para solicitar productos
        console.log('fetchProducts -> apiUrl:', apiUrl, 'selectedCategoryId:', selectedCategoryId, 'sort:', sort);
        const response = await fetch(apiUrl, {
          credentials: 'include'
        });
        if (!response.ok) {
          throw new Error(`Error HTTP: ${response.status} ${response.statusText}`);
        }
        const data = await response.json();

        if (data && data.success && Array.isArray(data.data)) {
          console.log("Datos recibidos, actualizando estado:", data.data); // Log justo antes de setProducts
          // 2. Actualiza el estado con los productos recibidos
          setProducts(data.data);
        } else {
          console.error('API no exitosa o formato incorrecto:', data);
          setError(data.message || 'No se pudieron cargar los productos.');
          setProducts([]);
        }
      } catch (err) {
        console.error('Error en fetchProducts:', err);
        setError(err.message || 'Error al conectar con el servidor.');
        setProducts([]);
      } finally {
        // 3. Termina la carga (SIEMPRE, incluso si hay error)
        setIsLoading(false);
        console.log("Carga finalizada."); // Log para ver cundo termina isLoading
      }
    };
    fetchProducts();
  }, [selectedCategoryId, sort, searchParam, filterMinPrice, filterMaxPrice, filterBrandIds]); // Dependencias actualizadas

  // Cargar marcas para el filtro
  useEffect(() => {
    const fetchBrands = async () => {
      try {
        const res = await fetch(backendUrl('get_brands.php'));
        if (res.ok) {
          const data = await res.json();
          if (Array.isArray(data)) setBrands(data);
        }
      } catch (e) {
        console.error("Error fetching brands:", e);
      }
    };
    fetchBrands();
  }, []);

  // Cargar categoras y aplicar parmetros iniciales
  useEffect(() => {
    const fetchCategories = async () => {
      try {
        const resp = await fetch(backendUrl('get_categories.php'));
        const cats = await resp.json();
        if (Array.isArray(cats)) {
          // Ocultar la categora de fallback "FALTA CATEGORIA" para usuarios no admin
          // Log para depuracin: ver qu categoras vienen del backend
          console.log('Categorias recibidas del backend:', cats);
          const isAdminRoute = location && location.pathname && String(location.pathname).startsWith('/admin');
          // Para evitar que un valor en localStorage contamine la vista pblica,
          // basamos la visibilidad del fallback solo en la ruta (admin vs pblico).
          const isAdmin = isAdminRoute;
          const filtered = cats.filter(cat => {
            if (!cat || !cat.name) return false;
            const nameNorm = String(cat.name).trim().toUpperCase();
            const isFallback = nameNorm === 'FALTA CATEGORIA' || nameNorm === 'FALTA CATEGORA';
            return isAdmin ? true : !isFallback;
          });
          console.log('Categorias filtradas (visibles):', filtered);
          setCategories(filtered);
          // Si viene un category_id/category por query, configurarlo una vez
          if (categoryParam) {
            // Si es numrico, salo; si es nombre, intenta buscar su id
            if (!isNaN(Number(categoryParam))) {
              // Si el parmetro es un ID numrico, solo seleccionarlo si est en las categoras visibles
              const idStr = String(Number(categoryParam));
              const existsVisible = filtered.some(fc => String(fc.id) === idStr);
              if (existsVisible) setSelectedCategoryId(idStr);
            } else {
              // Buscar por nombre en el listado original
              const match = cats.find(c => String(c.name) === String(categoryParam));
              if (match) {
                const nameNorm = String(match.name).trim().toUpperCase();
                const isFallback = nameNorm === 'FALTA CATEGORIA' || nameNorm === 'FALTA CATEGORA';
                // Solo seleccionar la categora fallback si estamos en ruta admin
                if (!isFallback || isAdminRoute) {
                  // Adems, asegurar que la categora est visible en el listado filtrado
                  const existsVisible = filtered.some(fc => String(fc.id) === String(match.id));
                  if (existsVisible) setSelectedCategoryId(String(match.id));
                }
              }
            }
          }
          if (sortParam) setSort(sortParam);
        }
      } catch (e) { /* opcional: manejar error silencioso */ }
    };
    fetchCategories();
    // Solo necesitamos correr esto cuando cambie el query param en la URL
  }, [categoryParam, sortParam, location]);

  // Si las categoras visibles cambian y la categora seleccionada ya no est en la lista,
  // limpiar la seleccin (evita que quede seleccionada una categora de fallback oculta)
  useEffect(() => {
    if (selectedCategoryId && categories.length > 0) {
      const exists = categories.some(c => String(c.id) === String(selectedCategoryId));
      if (!exists) setSelectedCategoryId('');
    }
  }, [categories, selectedCategoryId]);

  const pageTitle = 'Productos';

  // Log para depurar el estado en cada renderizado
  console.log(
    "Renderizando - isLoading:",
    isLoading,
    "error:",
    error,
    "products.length:",
    products.length,
    'selectedCategoryId:',
    selectedCategoryId,
    'categories:',
    categories
  );

  return (
    <div className="product-list-page-container">
      <h2>{pageTitle}</h2>

      {/* Filtros */}
      <div style={{ display: 'flex', gap: '1rem', marginBottom: '1rem' }}>
        <select
          className="form-select"
          value={selectedCategoryId}
          onChange={(e) => {
            const newValue = e.target.value;
            console.log('Select categora cambi a:', newValue);
            setSelectedCategoryId(newValue);
          }}
        >
          <option value="">Todas las categoras</option>
          {categories.map((c) => (
            <option key={c.id} value={c.id}>{c.name}</option>
          ))}
        </select>

        <select className="form-select" value={sort} onChange={(e) => setSort(e.target.value)}>
          <option value="newest">Recin llegados</option>
          <option value="price_asc">Menor precio</option>
          <option value="price_desc">Mayor precio</option>
          <option value="name">Nombre (A-Z)</option>
        </select>
      </div>

      {/* Botn y Panel de Filtros */}
      <div className="filters-container" style={{ marginBottom: '1rem' }}>
        <button
          className="btn-filters"
          onClick={() => setShowFilters(!showFilters)}
          style={{
            padding: '0.5rem 1rem',
            backgroundColor: '#333',
            color: 'white',
            border: '1px solid #444',
            borderRadius: '5px',
            cursor: 'pointer',
            display: 'flex',
            alignItems: 'center',
            gap: '0.5rem'
          }}
        >
          <i className="fa-solid fa-filter"></i> Filtros Avanzados {showFilters ? '' : ''}
        </button>

        {showFilters && (
          <div className="filters-panel" style={{
            marginTop: '0.5rem',
            padding: '1rem',
            backgroundColor: '#1e1e1e',
            border: '1px solid #333',
            borderRadius: '8px',
            display: 'flex',
            flexDirection: 'column',
            gap: '1rem'
          }}>
            {/* Rango de Precio */}
            <div>
              <h4 style={{ color: '#fff', marginBottom: '0.5rem', fontSize: '0.9rem' }}>Rango de Precio</h4>
              <div style={{ display: 'flex', gap: '0.5rem', alignItems: 'center' }}>
                <input
                  type="number"
                  placeholder="Mn"
                  value={filterMinPrice}
                  onChange={(e) => setFilterMinPrice(e.target.value)}
                  style={{ padding: '0.3rem', borderRadius: '4px', border: '1px solid #444', backgroundColor: '#2a2a2a', color: 'white', width: '80px' }}
                />
                <span style={{ color: '#888' }}>-</span>
                <input
                  type="number"
                  placeholder="Mx"
                  value={filterMaxPrice}
                  onChange={(e) => setFilterMaxPrice(e.target.value)}
                  style={{ padding: '0.3rem', borderRadius: '4px', border: '1px solid #444', backgroundColor: '#2a2a2a', color: 'white', width: '80px' }}
                />
              </div>
            </div>

            {/* Marcas */}
            <div>
              <h4 style={{ color: '#fff', marginBottom: '0.5rem', fontSize: '0.9rem' }}>Marcas</h4>
              <div style={{ display: 'flex', flexWrap: 'wrap', gap: '0.5rem' }}>
                {brands.map(brand => (
                  <label key={brand.id} style={{ display: 'flex', alignItems: 'center', gap: '0.3rem', color: '#ccc', fontSize: '0.85rem', cursor: 'pointer' }}>
                    <input
                      type="checkbox"
                      value={brand.id}
                      checked={filterBrandIds.includes(String(brand.id))}
                      onChange={(e) => {
                        const val = String(brand.id);
                        if (e.target.checked) {
                          setFilterBrandIds(prev => [...prev, val]);
                        } else {
                          setFilterBrandIds(prev => prev.filter(id => id !== val));
                        }
                      }}
                    />
                    {brand.name}
                  </label>
                ))}
              </div>
            </div>

            {/* Botones de Accin */}
            <div style={{ display: 'flex', gap: '0.5rem', marginTop: '0.5rem' }}>
              <button
                onClick={() => {
                  setFilterMinPrice('');
                  setFilterMaxPrice('');
                  setFilterBrandIds([]);
                }}
                style={{ padding: '0.3rem 0.8rem', backgroundColor: '#444', color: 'white', border: 'none', borderRadius: '4px', cursor: 'pointer', fontSize: '0.85rem' }}
              >
                Limpiar Filtros
              </button>
            </div>
          </div>
        )}
      </div>

      {/* 1. Muestra 'Cargando...' solo si isLoading es true */}
      {isLoading && <p>Cargando productos...</p>}

      {/* 2. Muestra error si existe y no est cargando */}
      {!isLoading && error && <p className="error-message">{error}</p>}

      {/* 3. Muestra productos o mensaje 'No encontrado' solo si NO est cargando Y NO hay error */}
      {!isLoading && !error && (
        <div className="product-list">
          {products.length > 0 ? (
            products.map((product) => (
              <ProductCard key={product.id} product={product} />
            ))
          ) : (
            // Este mensaje se muestra si !isLoading, !error, y products.length es 0
            <p>No se encontraron productos.</p>
          )}
        </div>
      )}
    </div>
  );
}

export default ProductListPage;
