import React, { createContext, useContext, useEffect, useMemo, useState } from 'react';
import { backendUrl } from '../config/api';

const CartContext = createContext(null);

function loadInitial() {
  try {
    const raw = localStorage.getItem('cart');
    const arr = raw ? JSON.parse(raw) : [];
    return Array.isArray(arr) ? arr : [];
  } catch {
    return [];
  }
}

export function CartProvider({ children }) {
  const [items, setItems] = useState(loadInitial);
  const [user, setUser] = useState(() => {
    try { return JSON.parse(localStorage.getItem('user')); } catch { return null; }
  });

  // Persist to localStorage
  useEffect(() => {
    try { localStorage.setItem('cart', JSON.stringify(items)); } catch { }
  }, [items]);

  // Sync with Backend on Mount (if logged in)
  useEffect(() => {
    const syncCart = async () => {
      if (!user) return;

      try {
        // 1. Send local items to merge with backend
        const response = await fetch(backendUrl('cart.php'), {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({
            action: 'sync',
            items: items // Send current local items
          })
        });

        // 2. Fetch updated cart from backend
        if (response.ok) {
          const getResponse = await fetch(backendUrl('cart.php'));
          const data = await getResponse.json();
          if (data.success && Array.isArray(data.cart)) {
            const mappedItems = data.cart.map(item => ({
              id: item.product_id,
              name: item.name,
              price: Number(item.price),
              image: item.image,
              quantity: Number(item.quantity),
              color: item.options?.color || null,
            }));
            setItems(mappedItems);
          }
        }
      } catch (error) {
        console.error("Error syncing cart:", error);
      }
    };

    syncCart();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const addItem = async (payload) => {
    const qty = Math.max(1, Number(payload.quantity || 1));
    let newQuantity = qty;

    setItems((prev) => {
      const keyMatch = (it) => it.id === payload.id && (it.color || '') === (payload.color || '');
      const idx = prev.findIndex(keyMatch);
      let next = [...prev];

      if (idx >= 0) {
        newQuantity = next[idx].quantity + qty;
        next[idx] = { ...next[idx], quantity: newQuantity };
        if (payload.image && !next[idx].image) next[idx].image = payload.image;
      } else {
        next = [...prev, {
          id: payload.id,
          name: payload.name,
          price: Number(payload.price) || 0,
          color: payload.color || null,
          quantity: qty,
          image: payload.image || null
        }];
      }
      return next;
    });

    if (user) {
      try {
        await fetch(backendUrl('cart.php'), {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({
            action: 'add',
            product_id: payload.id,
            quantity: newQuantity, // Send total quantity
            options: { color: payload.color }
          })
        });
      } catch (e) { console.error(e); }
    }
  };

  const removeItem = async (id, color = null) => {
    setItems((prev) => prev.filter((it) => !(it.id === id && (it.color || null) === (color || null))));

    if (user) {
      try {
        // Need to identify item by product_id + options
        // My backend DELETE implementation was a bit generic. 
        // Let's assume for now we delete by product_id and options if possible, 
        // or we need to fetch the cart_item_id.
        // To keep it simple and robust, let's just re-sync the whole cart state or 
        // update quantity to 0 (which backend handles as delete).
        await fetch(backendUrl('cart.php'), {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({
            action: 'update',
            product_id: id,
            quantity: 0,
            options: { color: color }
          })
        });
      } catch (e) { console.error(e); }
    }
  };

  const updateQuantity = async (id, color, quantity) => {
    const qty = Math.max(1, Number(quantity || 1));
    setItems((prev) => prev.map((it) => (it.id === id && (it.color || null) === (color || null)) ? { ...it, quantity: qty } : it));

    if (user) {
      try {
        await fetch(backendUrl('cart.php'), {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({
            action: 'update',
            product_id: id,
            quantity: qty,
            options: { color: color }
          })
        });
      } catch (e) { console.error(e); }
    }
  };

  const clear = async () => {
    setItems([]);
    if (user) {
      try {
        await fetch(backendUrl('cart.php'), {
          method: 'DELETE',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({ item_id: 'all' })
        });
      } catch (e) { console.error(e); }
    }
  };

  const { totalItems, totalPrice } = useMemo(() => {
    const totalItems = items.reduce((acc, it) => acc + (Number(it.quantity) || 0), 0);
    const totalPrice = items.reduce((acc, it) => acc + (Number(it.price) || 0) * (Number(it.quantity) || 0), 0);
    return { totalItems, totalPrice };
  }, [items]);

  const value = useMemo(() => ({ items, addItem, removeItem, updateQuantity, clear, totalItems, totalPrice }), [items, totalItems, totalPrice]);

  return <CartContext.Provider value={value}>{children}</CartContext.Provider>;
}

export function useCart() {
  const ctx = useContext(CartContext);
  if (!ctx) throw new Error('useCart must be used within CartProvider');
  return ctx;
}
