// src/App.js
import React from "react";
// Se elimin 'BrowserRouter as Router' ya que no se usa aqu
import { Route, Routes, Navigate, useLocation } from "react-router-dom";
import { HelmetProvider } from 'react-helmet-async';
import Navbar from "./components/Navbar";
import AdminNavbar from "./components/admin/AdminNavbar";
import Footer from "./components/Footer";
import Home from "./pages/Home";
import Login from "./pages/Login";
import Admin from "./pages/Admin";
import ProductListPage from "./pages/ProductListPage";
import ProductDetailPage from "./pages/ProductDetailPage";
import CartPage from "./pages/CartPage";
import Contact from "./pages/Contact";
import "./styles.css";

// Componente para proteger rutas administrativas (sin cambios)
function ProtectedRoute({ children }) {
  const isAuthenticated = localStorage.getItem('isAdminLoggedIn') === 'true';

  if (!isAuthenticated) {
    return <Navigate to="/login" replace />;
  }

  return children;
}

function App() {
  const location = useLocation();
  const isAdminRoute = location.pathname.startsWith('/admin');

  // Redireccin automtica para el subdominio 'gestion'
  // Redireccin automtica para el subdominio 'gestion'
  React.useEffect(() => {
    const hostname = window.location.hostname;
    console.log("Hostname detectado:", hostname); // Debug

    if (hostname.includes('gestion') &&
      !location.pathname.startsWith('/login') &&
      !location.pathname.startsWith('/admin')) {
      console.log("Redirigiendo a login..."); // Debug
      window.location.href = 'https://wolfftactical.cl/login';
    }
  }, [location]);

  return (
    <HelmetProvider>
      {isAdminRoute ? <AdminNavbar /> : <Navbar />}
      <div className="main-content">
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/login" element={<Login />} />
          <Route
            path="/admin/*"
            element={
              <ProtectedRoute>
                <Admin />
              </ProtectedRoute>
            }
          />
          <Route path="/productos" element={<ProductListPage />} />
          <Route path="/producto/:id" element={<ProductDetailPage />} />
          <Route path="/cart" element={<CartPage />} />
          <Route path="/contacto" element={<Contact />} />
          {/* Otras rutas pblicas (ejemplos comentados) */}
          {/* <Route path="/producto/:id" element={<ProductDetailPage />} /> */}
          {/* <Route path="/carrito" element={<CartPage />} /> */}
          {/* <Route path="/registro" element={<RegisterPage />} /> */}

          {/* Ruta comodn: Redirige a la pgina principal si no se encuentra la ruta */}
          <Route path="*" element={<Navigate to="/" replace />} />
        </Routes>
      </div>
      {!isAdminRoute && <Footer />}
    </HelmetProvider>
  );
}

export default App;
