import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { formatCLP, parseCLPInput } from '../../utils/formatters';
import { backendUrl } from '../../config/api';
import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';
import './AdminDesign.css'; // Import custom styles

function AgregarProducto() {
  const navigate = useNavigate();
  const [formData, setFormData] = useState({
    name: '',
    model: '',
    main_category: '',
    stock_option: 'preorder',
    stock_quantity: '',
    price: '',
    main_image: null,
    image_1: null,
    image_2: null,
    descripcion: '',
    incluye: '',
    incluye: '',
    video_url: '',
    brand_id: ''
  });

  const [categories, setCategories] = useState([]);
  const [brands, setBrands] = useState([]); // State for brands
  const [imagePreview, setImagePreview] = useState(null); // Vista previa solo para imagen principal
  const [additionalImages, setAdditionalImages] = useState([]); // Galera de imgenes adicionales
  const [additionalPreviews, setAdditionalPreviews] = useState([]); // Previews galera
  const [previewImage1, setPreviewImage1] = useState(null);
  const [previewImage2, setPreviewImage2] = useState(null);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState(null); // Para mostrar errores en la UI
  const [colors, setColors] = useState([]); // Array to manage colors
  const [newCategory, setNewCategory] = useState(''); // For creating new categories
  const [showNewCategoryInput, setShowNewCategoryInput] = useState(false);
  const [newBrand, setNewBrand] = useState(''); // For creating new brands
  const [showNewBrandInput, setShowNewBrandInput] = useState(false);
  const [toast, setToast] = useState({ show: false, title: '', message: '' });

  // --- Efecto para Cargar Categoras y Marcas Iniciales ---
  useEffect(() => {
    const fetchData = async () => {
      try {
        // Fetch Categories
        const catResponse = await fetch(backendUrl('get_categories.php'));
        if (catResponse.ok) {
          const catData = await catResponse.json();
          if (Array.isArray(catData)) {
            const filtered = catData.filter(cat => {
              if (!cat || !cat.name) return false;
              const nameNorm = String(cat.name).trim().toUpperCase();
              return !(nameNorm === 'FALTA CATEGORIA' || nameNorm === 'FALTA CATEGORA');
            });
            setCategories(filtered);
          }
        }

        // Fetch Brands
        const brandResponse = await fetch(backendUrl('get_brands.php'));
        if (brandResponse.ok) {
          const brandData = await brandResponse.json();
          if (Array.isArray(brandData)) {
            setBrands(brandData);
          }
        }
      } catch (err) {
        console.error('Error al cargar datos iniciales:', err);
      }
    };
    fetchData();
  }, []);

  // --- Manejador de Cambios en Inputs y Selects ---
  const handleChange = (e) => {
    const { name, value } = e.target;
    if (name === 'price') {
      handlePriceInputChange(value);
      return;
    }
    setFormData(prev => ({ ...prev, [name]: value }));
  };

  // Manejador para ReactQuill
  const handleQuillChange = (value, field) => {
    setFormData(prev => ({ ...prev, [field]: value }));
  };

  const handlePriceInputChange = (rawValue) => {
    if (rawValue.trim() === '') {
      setFormData(prev => ({ ...prev, price: '' }));
      return;
    }
    const numeric = parseCLPInput(rawValue);
    setFormData(prev => ({ ...prev, price: numeric }));
  };

  // --- Manejador de Cambio de Archivos (Imgenes) ---
  const handleFileChange = (e) => {
    const { name } = e.target;
    const file = e.target.files[0];
    setFormData((prev) => ({ ...prev, [name]: file || null }));

    if (name === 'main_image' && file) {
      const reader = new FileReader();
      reader.onloadend = () => setImagePreview(reader.result);
      reader.readAsDataURL(file);
    } else if (name === 'main_image' && !file) {
      setImagePreview(null);
    } else if (name === 'image_1') {
      if (file) {
        const r = new FileReader();
        r.onloadend = () => setPreviewImage1(r.result);
        r.readAsDataURL(file);
      } else { setPreviewImage1(null); }
    } else if (name === 'image_2') {
      if (file) {
        const r2 = new FileReader();
        r2.onloadend = () => setPreviewImage2(r2.result);
        r2.readAsDataURL(file);
      } else { setPreviewImage2(null); }
    }
  };

  // --- Manejador para imgenes adicionales (mltiples) ---
  const handleAdditionalImagesChange = (e) => {
    const files = Array.from(e.target.files || []);
    setAdditionalImages(prev => [...prev, ...files]);

    // Generar previews
    const newPreviews = files.map(file => ({ name: file.name, url: URL.createObjectURL(file) }));
    setAdditionalPreviews(prev => [...prev, ...newPreviews]);
  };

  const removeAdditionalImage = (index) => {
    setAdditionalImages(prev => prev.filter((_, i) => i !== index));
    setAdditionalPreviews(prev => prev.filter((_, i) => i !== index));
  };

  // Add a new color
  const handleAddColor = () => {
    if (colors.length < 8) {
      setColors([...colors, { color: '#000000', image: null }]);
    }
  };

  // Update color or image in the colors array
  const handleColorChange = (index, field, value) => {
    const updatedColors = [...colors];
    updatedColors[index][field] = value;
    setColors(updatedColors);
  };

  // Remove a color
  const handleRemoveColor = (index) => {
    setColors(colors.filter((_, i) => i !== index));
  };

  // Handle new category creation
  const handleCreateCategory = async () => {
    if (newCategory.trim()) {
      try {
        const response = await fetch(backendUrl('create_category.php'), {
          method: 'POST',
          body: JSON.stringify({ name: newCategory }),
          headers: { 'Content-Type': 'application/json' },
          credentials: 'include',
        });
        const data = await response.json();
        if (data.success) {
          setCategories([...categories, data.category]);
          setFormData(prev => ({ ...prev, main_category: data.category.id }));
          setNewCategory('');
          setShowNewCategoryInput(false);
          showToast('Categora Creada!', `Se ha aadido "${data.category.name}" a la lista.`);
        } else {
          throw new Error(data.message);
        }
      } catch (err) {
        console.error('Error creating category:', err);
        setError('Error al crear categora');
      }
    }
  };

  // Handle new brand creation
  const handleCreateBrand = async () => {
    if (newBrand.trim()) {
      try {
        const response = await fetch(backendUrl('create_brand.php'), {
          method: 'POST',
          body: JSON.stringify({ name: newBrand }),
          headers: { 'Content-Type': 'application/json' },
          credentials: 'include',
        });
        const data = await response.json();
        if (data.success) {
          setBrands([...brands, data.brand]);
          setFormData(prev => ({ ...prev, brand_id: data.brand.id }));
          setNewBrand('');
          setShowNewBrandInput(false);
          showToast('Marca Creada!', `Se ha aadido "${data.brand.name}" a la lista.`);
        } else {
          throw new Error(data.message);
        }
      } catch (err) {
        console.error('Error creating brand:', err);
        setError('Error al crear marca');
      }
    }
  };

  const showToast = (title, message) => {
    setToast({ show: true, title, message });
    setTimeout(() => setToast({ show: false, title: '', message: '' }), 3000);
  };

  // --- Manejador de Envo del Formulario ---
  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsLoading(true);
    setError(null);

    const formDataToSend = new FormData();
    Object.entries(formData).forEach(([key, value]) => {
      if (value !== null && value !== '' && !key.startsWith('new_')) {
        formDataToSend.append(key, value);
      }
    });

    if (additionalImages.length > 0) {
      additionalImages.forEach((file) => {
        if (file) formDataToSend.append('additional_images[]', file);
      });
    }

    if (colors.length > 0) {
      colors.forEach((color, index) => {
        if (color.color) {
          formDataToSend.append(`colors[${index}][hex]`, color.color);
          formDataToSend.append(`colors[${index}][name]`, color.color);
          if (color.image instanceof File) {
            formDataToSend.append(`color_image_${index}`, color.image);
          }
        }
      });
    }

    try {
      const response = await fetch(backendUrl('agregar_producto.php'), {
        method: 'POST',
        credentials: 'include',
        body: formDataToSend
      });

      const rawText = await response.text();
      let data;
      try {
        data = JSON.parse(rawText);
      } catch (parseErr) {
        throw new Error(`Respuesta invlida del servidor. Texto: ${rawText.substring(0, 200)}...`);
      }

      if (!response.ok || !data.success) {
        setError(data.message || `Error ${response.status}`);
        setIsLoading(false);
        return;
      }

      showToast('Producto Guardado!', 'El producto se ha aadido al inventario.');
      setTimeout(() => navigate('/admin/productos'), 1500);

    } catch (err) {
      console.error('Error en handleSubmit:', err);
      setError(err.message || 'Ocurri un error inesperado.');
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="antialiased min-h-screen flex bg-slate-900 text-slate-200 font-sans">
      {/* Sidebar Simulado (Solo visual, oculto en mvil) */}
      <aside className="w-64 bg-slate-850 border-r border-slate-700 hidden lg:flex flex-col fixed h-full z-10">
        <div className="p-6 flex items-center gap-3 border-b border-slate-700/50">
          <div className="w-8 h-8 bg-blue-600 rounded flex items-center justify-center font-bold text-white">W</div>
          <span className="font-bold text-lg tracking-wide">WOLFF<span className="text-blue-500">TACTICAL</span></span>
        </div>
        <nav className="flex-1 p-4 space-y-2 mt-4">
          <button onClick={() => navigate('/admin/dashboard')} className="w-full flex items-center gap-3 px-4 py-3 text-slate-400 hover:text-white hover:bg-slate-800 rounded-lg transition">
            <i className="fa-solid fa-gauge w-5 text-center"></i> Dashboard
          </button>
          <button onClick={() => navigate('/admin/productos')} className="w-full flex items-center gap-3 px-4 py-3 bg-blue-600/10 text-blue-400 border border-blue-600/20 rounded-lg transition">
            <i className="fa-solid fa-boxes-stacked w-5 text-center"></i> Inventario
          </button>
        </nav>
      </aside>

      {/* Main Content */}
      <main className="flex-1 lg:ml-64 p-4 md:p-8 overflow-y-auto">
        {/* Top Bar & Header */}
        <header className="flex flex-col md:flex-row justify-between items-start md:items-center mb-8 gap-4">
          <div>
            <div className="flex items-center gap-2 text-sm text-slate-400 mb-1">
              <span>Admin</span> <i className="fa-solid fa-chevron-right text-xs"></i> <span>Productos</span> <i className="fa-solid fa-chevron-right text-xs"></i> <span className="text-blue-400">Nuevo</span>
            </div>
            <h1 className="text-3xl font-bold text-white tracking-tight">Agregar Nuevo Producto</h1>
          </div>

          <div className="flex gap-3 w-full md:w-auto">
            <button type="button" onClick={() => navigate('/admin/productos')} className="flex-1 md:flex-none px-5 py-2.5 rounded-lg border border-slate-600 text-slate-300 hover:bg-slate-800 hover:text-white transition font-medium text-sm">
              Descartar
            </button>
            <button onClick={handleSubmit} type="button" disabled={isLoading} className="flex-1 md:flex-none px-6 py-2.5 rounded-lg bg-blue-600 text-white font-semibold hover:bg-blue-500 shadow-lg shadow-blue-900/40 transition flex items-center justify-center gap-2 text-sm">
              <i className="fa-solid fa-save"></i> {isLoading ? 'Guardando...' : 'Publicar Producto'}
            </button>
          </div>
        </header>

        {error && <div className="bg-red-500/10 border border-red-500 text-red-500 p-4 rounded-lg mb-6">{error}</div>}

        {/* Form Layout */}
        <form id="productForm" className="grid grid-cols-1 xl:grid-cols-3 gap-6 md:gap-8 max-w-7xl mx-auto" onSubmit={handleSubmit}>

          {/* LEFT COLUMN (Main Data) */}
          <div className="xl:col-span-2 space-y-6">

            {/* Section: General Info */}
            <div className="bg-slate-850 rounded-xl border border-slate-700/50 shadow-xl overflow-hidden">
              <div className="p-6 border-b border-slate-700/50">
                <h2 className="text-lg font-semibold text-white flex items-center gap-2">
                  <i className="fa-solid fa-circle-info text-blue-500 text-sm"></i> Informacin Bsica
                </h2>
              </div>
              <div className="p-6 space-y-5">
                {/* Product Name */}
                <div>
                  <label className="block text-sm font-medium text-slate-300 mb-1.5">Nombre del Producto <span className="text-red-500">*</span></label>
                  <input type="text" name="name" value={formData.name} onChange={handleChange} className="w-full rounded-lg px-4 py-3 input-tactical" placeholder="Ej: Chaleco Tctico Porta Placas V2" required />
                </div>

                {/* Row: Model & Video */}
                <div className="grid grid-cols-1 md:grid-cols-2 gap-5">
                  <div>
                    <label className="block text-sm font-medium text-slate-300 mb-1.5">Modelo / SKU</label>
                    <div className="relative">
                      <span className="absolute left-3 top-3.5 text-slate-500 text-sm"><i className="fa-solid fa-barcode"></i></span>
                      <input type="text" name="model" value={formData.model} onChange={handleChange} className="w-full rounded-lg pl-9 pr-4 py-3 input-tactical" placeholder="WT-XXX-000" />
                    </div>
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-slate-300 mb-1.5">Video Review (URL)</label>
                    <div className="relative">
                      <span className="absolute left-3 top-3.5 text-slate-500 text-sm"><i className="fa-brands fa-youtube"></i></span>
                      <input type="url" name="video_url" value={formData.video_url} onChange={handleChange} className="w-full rounded-lg pl-9 pr-4 py-3 input-tactical" placeholder="https://youtube.com/..." />
                    </div>
                  </div>
                </div>

                {/* Description */}
                <div>
                  <label className="block text-sm font-medium text-slate-300 mb-1.5">Descripcin Tcnica</label>
                  <div className="text-slate-900">
                    <ReactQuill theme="snow" value={formData.descripcion} onChange={(val) => handleQuillChange(val, 'descripcion')} className="bg-white rounded-lg" />
                  </div>
                </div>
              </div>
            </div>

            {/* Section: Media */}
            <div className="bg-slate-850 rounded-xl border border-slate-700/50 shadow-xl overflow-hidden">
              <div className="p-6 border-b border-slate-700/50 flex justify-between items-center">
                <h2 className="text-lg font-semibold text-white flex items-center gap-2">
                  <i className="fa-solid fa-images text-purple-500 text-sm"></i> Galera Multimedia
                </h2>
                <span className="text-xs bg-slate-700 text-slate-300 px-2 py-1 rounded">Max 5MB por img</span>
              </div>

              <div className="p-6">
                {/* Main Image */}
                <div className="mb-6">
                  <label className="block text-sm font-medium text-slate-300 mb-1.5">Imagen Principal <span className="text-red-500">*</span></label>
                  <input type="file" name="main_image" onChange={handleFileChange} accept="image/*" className="block w-full text-sm text-slate-500 file:mr-4 file:py-2 file:px-4 file:rounded-full file:border-0 file:text-sm file:font-semibold file:bg-blue-50 file:text-blue-700 hover:file:bg-blue-100" required />
                  {imagePreview && (
                    <div className="mt-2 relative w-40 h-40 rounded-lg overflow-hidden border border-slate-600">
                      <img src={imagePreview} alt="Main Preview" className="w-full h-full object-cover" />
                    </div>
                  )}
                </div>

                {/* Drop Zone for Additional Images */}
                <div className="drop-zone rounded-xl p-8 md:p-12 text-center cursor-pointer relative group bg-slate-900/50">
                  <input type="file" multiple accept="image/*" onChange={handleAdditionalImagesChange} className="absolute inset-0 w-full h-full opacity-0 cursor-pointer z-10" />
                  <div className="pointer-events-none transition-transform group-hover:scale-105 duration-300">
                    <div className="w-16 h-16 bg-slate-800 rounded-full flex items-center justify-center mx-auto mb-4 border border-slate-700 group-hover:border-blue-500 group-hover:text-blue-500 transition-colors">
                      <i className="fa-solid fa-cloud-arrow-up text-2xl text-slate-400 group-hover:text-blue-500"></i>
                    </div>
                    <h3 className="text-lg font-medium text-white mb-1">Arrastra y suelta tus imgenes</h3>
                    <p className="text-slate-500 text-sm">Soporta JPG, PNG, WEBP</p>
                  </div>
                </div>

                {/* Preview Grid */}
                <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 gap-4 mt-6">
                  {additionalPreviews.map((p, index) => (
                    <div key={index} className="relative aspect-square rounded-lg overflow-hidden border border-slate-600 group bg-slate-800">
                      <img src={p.url} alt={p.name} className="w-full h-full object-cover" />
                      <div className="absolute inset-0 bg-slate-900/60 opacity-0 group-hover:opacity-100 transition-opacity flex items-center justify-center gap-2 backdrop-blur-sm">
                        <button type="button" onClick={() => removeAdditionalImage(index)} className="text-white hover:text-red-400 transition">
                          <i className="fa-solid fa-trash-can text-xl"></i>
                        </button>
                      </div>
                    </div>
                  ))}
                </div>

                {/* Extra Images 1 & 2 */}
                <div className="grid grid-cols-1 md:grid-cols-2 gap-5 mt-6 pt-6 border-t border-slate-700/50">
                  <div>
                    <label className="block text-sm font-medium text-slate-300 mb-1.5">Imagen Adicional 1 (Opcional)</label>
                    <input type="file" name="image_1" onChange={handleFileChange} accept="image/*" className="block w-full text-sm text-slate-500 file:mr-4 file:py-2 file:px-4 file:rounded-full file:border-0 file:text-sm file:font-semibold file:bg-slate-50 file:text-slate-700 hover:file:bg-slate-100" />
                    {previewImage1 && <img src={previewImage1} alt="Preview 1" className="mt-2 h-20 rounded border border-slate-600" />}
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-slate-300 mb-1.5">Imagen Adicional 2 (Opcional)</label>
                    <input type="file" name="image_2" onChange={handleFileChange} accept="image/*" className="block w-full text-sm text-slate-500 file:mr-4 file:py-2 file:px-4 file:rounded-full file:border-0 file:text-sm file:font-semibold file:bg-slate-50 file:text-slate-700 hover:file:bg-slate-100" />
                    {previewImage2 && <img src={previewImage2} alt="Preview 2" className="mt-2 h-20 rounded border border-slate-600" />}
                  </div>
                </div>
              </div>
            </div>

            {/* Section: Colors / Variants */}
            <div className="bg-slate-850 rounded-xl border border-slate-700/50 shadow-xl overflow-hidden">
              <div className="p-6 border-b border-slate-700/50 flex justify-between items-center">
                <h2 className="text-lg font-semibold text-white flex items-center gap-2">
                  <i className="fa-solid fa-palette text-green-500 text-sm"></i> Variantes de Color
                </h2>
                <button type="button" onClick={handleAddColor} className="text-xs bg-slate-700 hover:bg-slate-600 text-white px-3 py-1.5 rounded transition flex items-center gap-2">
                  <i className="fa-solid fa-plus"></i> Aadir
                </button>
              </div>
              <div className="p-6">
                <div className="space-y-3">
                  {colors.map((color, index) => (
                    <div key={index} className="flex gap-3 items-center group animate-[fadeIn_0.3s_ease-out]">
                      <div className="w-8 flex justify-center text-slate-600"><i className="fa-solid fa-grip-vertical"></i></div>
                      <input type="text" value={color.name || ''} onChange={(e) => handleColorChange(index, 'name', e.target.value)} className="flex-1 rounded-lg px-4 py-2 input-tactical text-sm" placeholder="Nombre del color (ej: Negro Mate)" />
                      <div className="relative">
                        <input type="color" value={color.color} onChange={(e) => handleColorChange(index, 'color', e.target.value)} className="w-10 h-10 rounded cursor-pointer bg-transparent border-0 p-0 overflow-hidden" />
                      </div>
                      <input type="file" accept="image/*" onChange={(e) => handleColorChange(index, 'image', e.target.files[0])} className="text-xs text-slate-500 w-24" />
                      <button type="button" onClick={() => handleRemoveColor(index)} className="w-8 h-8 rounded flex items-center justify-center text-slate-500 hover:text-red-400 hover:bg-slate-800 transition" title="Eliminar">
                        <i className="fa-solid fa-trash-can"></i>
                      </button>
                    </div>
                  ))}
                </div>
                <p className="text-xs text-slate-500 mt-4 ml-1"><i className="fa-solid fa-lightbulb text-yellow-600 mr-1"></i> Consejo: Usa el selector de color para referencia visual en el sitio.</p>
              </div>
            </div>

          </div>

          {/* RIGHT COLUMN (Side Data) */}
          <div className="space-y-6">

            {/* Pricing Card */}
            <div className="bg-slate-850 rounded-xl border border-slate-700/50 shadow-xl overflow-hidden sticky top-6">
              <div className="p-5 border-b border-slate-700/50 bg-slate-800/30">
                <h3 className="text-sm font-bold text-slate-400 uppercase tracking-wider">Precio e Inventario</h3>
              </div>

              <div className="p-6 space-y-6">
                {/* Price */}
                <div>
                  <label className="block text-xs text-slate-400 font-bold mb-2 uppercase">Precio de Venta (CLP)</label>
                  <div className="relative group">
                    <span className="absolute left-4 top-3 text-emerald-500 font-bold text-lg">$</span>
                    <input type="text" value={formData.price === '' ? '' : formatCLP(formData.price)} onChange={(e) => handlePriceInputChange(e.target.value)} className="w-full rounded-lg pl-8 pr-4 py-3 bg-slate-900 border border-slate-600 text-emerald-400 font-bold text-xl focus:outline-none focus:border-emerald-500 focus:ring-1 focus:ring-emerald-500 transition" placeholder="0" />
                    <span className="absolute right-4 top-4 text-xs text-slate-500">CLP</span>
                  </div>
                </div>

                <hr className="border-slate-700/50" />

                {/* Stock Status */}
                <div className="space-y-3">
                  <label className="block text-xs text-slate-400 font-bold mb-1 uppercase">Disponibilidad</label>

                  <label className={`flex items-start gap-3 p-3 rounded-lg border border-slate-700/50 bg-slate-800/30 cursor-pointer hover:border-blue-500/50 transition relative overflow-hidden ${formData.stock_option === 'instock' ? 'border-blue-500 ring-1 ring-blue-500/20' : ''}`}>
                    <input type="radio" name="stock_option" value="instock" checked={formData.stock_option === 'instock'} onChange={handleChange} className="mt-1 text-blue-600 focus:ring-blue-500 bg-slate-700 border-slate-600" />
                    <div>
                      <span className="block text-sm font-medium text-white">En Stock</span>
                      <span className="block text-xs text-slate-500">Envo inmediato desde bodega.</span>
                    </div>
                  </label>

                  <label className={`flex items-start gap-3 p-3 rounded-lg border border-slate-700/50 bg-slate-800/30 cursor-pointer hover:border-yellow-500/50 transition relative overflow-hidden ${formData.stock_option === 'preorder' ? 'border-yellow-500 ring-1 ring-yellow-500/20' : ''}`}>
                    <input type="radio" name="stock_option" value="preorder" checked={formData.stock_option === 'preorder'} onChange={handleChange} className="mt-1 text-yellow-600 focus:ring-yellow-500 bg-slate-700 border-slate-600" />
                    <div>
                      <span className="block text-sm font-medium text-white">Por Encargo</span>
                      <span className="block text-xs text-slate-500">Sujeto a importacin (15-20 das).</span>
                    </div>
                  </label>

                  {formData.stock_option === 'instock' && (
                    <div className="mt-2">
                      <label className="block text-xs text-slate-400 mb-1">Cantidad</label>
                      <input type="number" name="stock_quantity" value={formData.stock_quantity} onChange={handleChange} className="w-full rounded-lg px-3 py-2 input-tactical" min="0" />
                    </div>
                  )}
                </div>
              </div>
            </div>

            {/* Categorization Card */}
            <div className="bg-slate-850 rounded-xl border border-slate-700/50 shadow-xl overflow-hidden">
              <div className="p-5 border-b border-slate-700/50 bg-slate-800/30">
                <h3 className="text-sm font-bold text-slate-400 uppercase tracking-wider">Organizacin</h3>
              </div>
              <div className="p-6 space-y-5">
                {/* Brand Selection */}
                <div>
                  <div className="flex justify-between items-center mb-2">
                    <label className="block text-xs text-slate-400 font-bold uppercase">Marca</label>
                    <button type="button" onClick={() => setShowNewBrandInput(!showNewBrandInput)} className="text-xs text-blue-400 hover:text-blue-300 transition flex items-center gap-1">
                      <i className="fa-solid fa-plus"></i> Nueva
                    </button>
                  </div>

                  <select name="brand_id" value={formData.brand_id} onChange={handleChange} className="w-full rounded-lg px-4 py-2.5 input-tactical appearance-none cursor-pointer">
                    <option value="">Seleccionar Marca...</option>
                    {brands.map(brand => (
                      <option key={brand.id} value={brand.id}>{brand.name}</option>
                    ))}
                  </select>

                  {showNewBrandInput && (
                    <div className="mt-2 flex gap-2 fade-in-down">
                      <input type="text" value={newBrand} onChange={(e) => setNewBrand(e.target.value)} className="flex-1 rounded-lg px-3 py-2 input-tactical text-sm border-blue-500 border" placeholder="Nombre nueva marca..." />
                      <button type="button" onClick={handleCreateBrand} className="bg-blue-600 hover:bg-blue-500 text-white px-3 py-2 rounded-lg text-sm transition shadow-lg shadow-blue-500/20" title="Guardar">
                        <i className="fa-solid fa-check"></i>
                      </button>
                      <button type="button" onClick={() => setShowNewBrandInput(false)} className="text-slate-500 hover:text-white px-2 transition" title="Cancelar">
                        <i className="fa-solid fa-xmark"></i>
                      </button>
                    </div>
                  )}
                </div>

                {/* Category with Quick Add */}
                <div>
                  <div className="flex justify-between items-center mb-2">
                    <label className="block text-xs text-slate-400 font-bold uppercase">Categora Principal</label>
                    <button type="button" onClick={() => setShowNewCategoryInput(!showNewCategoryInput)} className="text-xs text-blue-400 hover:text-blue-300 transition flex items-center gap-1">
                      <i className="fa-solid fa-plus"></i> Nueva
                    </button>
                  </div>

                  <select name="main_category" value={formData.main_category} onChange={handleChange} className="w-full rounded-lg px-4 py-2.5 input-tactical appearance-none cursor-pointer">
                    <option value="" disabled>Seleccionar...</option>
                    {categories.map(cat => (
                      <option key={cat.id} value={cat.id}>{cat.name}</option>
                    ))}
                  </select>

                  {showNewCategoryInput && (
                    <div className="mt-2 flex gap-2 fade-in-down">
                      <input type="text" value={newCategory} onChange={(e) => setNewCategory(e.target.value)} className="flex-1 rounded-lg px-3 py-2 input-tactical text-sm border-blue-500 border" placeholder="Nombre nueva categora..." />
                      <button type="button" onClick={handleCreateCategory} className="bg-blue-600 hover:bg-blue-500 text-white px-3 py-2 rounded-lg text-sm transition shadow-lg shadow-blue-500/20" title="Guardar">
                        <i className="fa-solid fa-check"></i>
                      </button>
                      <button type="button" onClick={() => setShowNewCategoryInput(false)} className="text-slate-500 hover:text-white px-2 transition" title="Cancelar">
                        <i className="fa-solid fa-xmark"></i>
                      </button>
                    </div>
                  )}
                </div>

                {/* Includes */}
                <div>
                  <label className="block text-xs text-slate-400 font-bold mb-2 uppercase">Qu incluye la caja?</label>
                  <div className="text-slate-900">
                    <ReactQuill theme="snow" value={formData.incluye} onChange={(val) => handleQuillChange(val, 'incluye')} className="bg-white rounded-lg" />
                  </div>
                </div>
              </div>
            </div>

          </div>
        </form>
      </main>

      {/* TOAST NOTIFICATION */}
      <div className={`fixed bottom-5 right-5 bg-emerald-600 text-white px-6 py-4 rounded-lg shadow-2xl transform transition-transform duration-300 flex items-center gap-3 z-50 ${toast.show ? 'translate-y-0' : 'translate-y-24 opacity-0'}`}>
        <i className="fa-solid fa-circle-check text-xl"></i>
        <div>
          <h4 className="font-bold text-sm">{toast.title}</h4>
          <p className="text-xs text-emerald-100">{toast.message}</p>
        </div>
      </div>
    </div>
  );
}

export default AgregarProducto;
