import React from 'react';
import { GoogleOAuthProvider, GoogleLogin } from '@react-oauth/google';
import { jwtDecode } from "jwt-decode";
import { backendUrl } from '../config/api';

const GoogleLoginButton = ({ onLoginSuccess }) => {
    const handleSuccess = async (credentialResponse) => {
        try {
            const { credential } = credentialResponse;

            // Enviar token al backend para verificacin y creacin de sesin
            const response = await fetch(backendUrl('google_login.php'), {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ token: credential }),
            });

            const data = await response.json();

            if (data.success) {
                // Notificar al padre (App.js o Login.js)
                if (onLoginSuccess) {
                    onLoginSuccess(data);
                }
            } else {
                console.error('Error en login de backend:', data.message);
                alert('Error al iniciar sesin: ' + data.message);
            }
        } catch (error) {
            console.error('Error de red:', error);
            alert('Error de conexin al intentar iniciar sesin.');
        }
    };

    const handleError = () => {
        console.error('Login Failed');
        alert('Fall el inicio de sesin con Google.');
    };

    return (
        <div className="google-login-container" style={{ margin: '20px 0', display: 'flex', justifyContent: 'center' }}>
            <GoogleLogin
                onSuccess={handleSuccess}
                onError={handleError}
                theme="filled_black"
                shape="pill"
                text="continue_with"
                locale="es"
            />
        </div>
    );
};

export default GoogleLoginButton;
