import React, { useState } from "react";
import "./Subscribe.css";
import { backendUrl } from "../config/api";

function Subscribe() {
  const [email, setEmail] = useState("");
  const [message, setMessage] = useState("");
  const [loading, setLoading] = useState(false);

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!email) return;

    setLoading(true);
    setMessage("");

    try {
      const response = await fetch(backendUrl("subscribe.php"), {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ email }),
      });

      const data = await response.json();
      setMessage(data.message);
      if (data.success) {
        setEmail("");
      }
    } catch (error) {
      setMessage("Error al suscribirse. Intente nuevamente.");
    } finally {
      setLoading(false);
    }
  };

  return (
    <section className="subscribe">
      <h2>Suscrbete para recibir ofertas especiales</h2>
      <form onSubmit={handleSubmit} style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', gap: '10px' }}>
        <input
          type="email"
          placeholder="Ingresa Tu email"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          disabled={loading}
          required
        />
        <button type="submit" disabled={loading}>
          {loading ? "Suscribiendo..." : "Suscribir Correo"}
        </button>
        {message && <p style={{ marginTop: "10px", fontSize: "0.9rem", color: message.includes("Error") || message.includes("Solo") ? "#ff6b6b" : "#4caf50" }}>{message}</p>}
      </form>
    </section>
  );
}

export default Subscribe;
